.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.location-input-container{
  min-height: 10vh;
  background-color: darkgrey;
  display: flex;
  align-items: center;
  justify-content: center;
}

.location-input{
  display: block;
  min-width: 40%;
  font-size: medium;
  margin-right: 15px;
}

.location-input::placeholder{
  color: black;
}

.error{
  font-size: 60px;
  color: red;
}

.preloader img{
  height: 100px;
  width: 100px;
}